import React from "react";
import { Link } from "react-scroll";
import styled from "styled-components";
import { ButtonMain } from './styles/ButtonMain'

const Container = styled.div`
  margin-right: 50px;
  width: 220px;
  padding: 20px;
  -webkit-box-shadow: 0px 0px 17px -11px rgba(0, 0, 0, 0.58);
  box-shadow: 0px 0px 17px -11px rgba(0, 0, 0, 0.58);
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media only screen and (max-width: 700px) {
    margin-right: 0;
    margin-bottom: 10px;
    padding: 10px;
  }
`;

const PriceContainer = styled.div`
  display: flex;
  align-items: center;
`;

const Price = styled.span`
  font-weight: bold;
  font-size: 50px;
  @media only screen and (max-width: 700px) {
    font-size: 30px;
  }
`;

const Type = styled.button`
  padding: 10px;
  margin: 10px 0;
  border: 1.5px solid crimson;
  color: crimson;
  background-color: white;
  border-radius: 20px;
  @media only screen and (max-width: 700px) {
    display: none;
  }
`;

const List = styled.ul`
  list-style: none;
`;

const ListItem = styled.li`
  margin: 30px 0;
  @media only screen and (max-width: 700px) {
    margin: 10px;
    font-size: 12px;
  }
`;



const PriceCard = ({ price, type, access, extra }) => {
  return (
    <Container>
      <PriceContainer>
        $<Price>{price}</Price>/month
      </PriceContainer>
      <Type>{type} Plan</Type>
      <List>
        <ListItem>{access} License</ListItem>
        <ListItem>Exclusive Support</ListItem>
        <ListItem>24/7 Exclusive Support</ListItem>
        <ListItem>{extra}</ListItem>{" "}
      </List>
      <ButtonMain><Link to="contact" spy={true} smooth={true}>Join Now</Link></ButtonMain>
    </Container>
  );
};



export default PriceCard;
